
  import { mapGetters, mapActions, mapMutations } from 'vuex'
  import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

  export default {
    components: {
      Cell,
      Group
    },
    data () {
      return {
        model: {
          stationId: 0,
          shiftId: 0
        }
      }
    },
    computed: {
      ...mapGetters([
        'CurShiftInfo'
      ]),
      stations () {
        return this.CurShiftInfo.stations
      }
    },
    methods: {
      ...mapActions([
        'QueryStationChk'
      ]),
      ...mapMutations([
        'isLoading'
      ]),
      changeStation (el) {
        const $this = this
        this.$vux.confirm.show({
		  title: '是否更换站点？',
		  onCancel () {},
		  onConfirm () {
		    $this.model.stationId = el.id
		    $this.isLoading({ isLoading: true })
		    $this.QueryStationChk($this.model).then((res) => {
		      $this.isLoading({ isLoading: false })
		      $this.$store.state.responseData['QueryStationChk'].stationName =  el.name
		      $this.$store.state.responseData['QueryStationChk'].stationId = $this.model.stationId
		      $this.$router.push('/')
		    })
		  }
		})
      }
    },
    created () {
      this.model = {
        stationId: this.stations[0].id || 0,
        shiftId: this.CurShiftInfo.shiftId || 0
      }
      if (this.stations.length === 0) {
        this.$store.state.comm.loadMore = 'notdata'
      }
    }
  }



